import React, { useState } from 'react';
import { get, parseRequestOptionsFromJSON } from '@github/webauthn-json/browser-ponyfill';
import { Alert, Button, Stack, TextField, Typography } from '@mui/material';
import { useForm } from 'react-hook-form';
import GppBadIcon from '@mui/icons-material/GppBad';
import { useNavigate } from 'react-router-dom';

const login = async (email?: string) => {
  let extraData = {};

  if (email) {
    extraData = {
      headers: {
        'Content-Type': 'application/json',
      },
      body: JSON.stringify({
        email,
      }),
    };
  }

  const request = await fetch('/auth/request-login', {
    ...extraData,
    method: 'POST',
  });

  if (!request.ok) {
    throw (request);
  }

  const authOptions = await request.json();
  const response = (
    await get(parseRequestOptionsFromJSON(authOptions))
  ).toJSON();

  const res = await fetch('/auth/login', {
    method: 'POST',
    headers: {
      'Content-Type': 'application/json',
    },
    body: JSON.stringify({
      id: response.rawId,
      user: response.response.userHandle,
      data: response.response.clientDataJSON,
    }),
  });

  if (!res.ok) {
    throw (res);
  }

  const { userId } = await res.json();
  localStorage.setItem('userId', userId);
  return true;
};

function LoginForm() {
  const [error, setError] = useState();
  const navigate = useNavigate();

  const { register, handleSubmit } = useForm();
  const onSubmit = async (data: any) => {
    try {
      const result = await login(data.email);
      if (result) {
        navigate('/private');
      }
    } catch (e: any) {
      setError(e.statusText);
    }
  };

  return <>
    {error && <Alert icon={<GppBadIcon fontSize="inherit" />} severity="error">
      {error}
    </Alert>}
    <Typography textAlign={'center'} variant={'h5'}>Login</Typography>
    <Stack
      spacing={1}
      component="form"
      onSubmit={handleSubmit(onSubmit)}
      noValidate
      autoComplete="off"
    >
      <TextField {...register('email')}
                 id={'email'}
                 key={'email'}
                 name={'email'}
                 placeholder={'Email'}
                 label={'Email'}
                 size="small"
      />
      <Button size="small" variant="contained" type="submit">Login</Button>
    </Stack>
  </>;
}

export default LoginForm;
