import {
  parseCreationOptionsFromJSON,
  supported,
  create,
} from '@github/webauthn-json/browser-ponyfill';
import { Alert, Button, Stack, TextField, Typography } from '@mui/material';
import React, { useEffect, useState } from 'react';
import { useForm } from 'react-hook-form';
import GppBadIcon from '@mui/icons-material/GppBad';

const registration = async (registerEmail: string, registerDisplayName: string) => {
  const request = await fetch('/auth/request-register', {
    method: 'POST',
    headers: {
      'Content-Type': 'application/json',
    },
    body: JSON.stringify({
      email: registerEmail,
      displayName: registerDisplayName,
    }),
  });

  if (!request.ok) {
    throw (request);
  }

  const authOptions = await request.json();

  try {
    const result = await create(parseCreationOptionsFromJSON(authOptions));
    const response = result.toJSON();

    const res = await fetch('/auth/register', {
      method: 'POST',
      headers: {
        'Content-Type': 'application/json',
      },
      body: JSON.stringify({
        id: response.rawId,
        data: response.response.clientDataJSON,
        type: response.type,
      }),
    });

    if (!res.ok) {
      throw (res);
    }

    return true;

  } catch (e) {
    await fetch('/auth/abort-register', {
      method: 'POST',
      headers: {
        'Content-Type': 'application/json',
      },
      body: JSON.stringify({
        challenge: authOptions.publicKey.challenge,
      }),
    });
    throw (e);
  }
};

function RegistrationForm() {
  const { register, handleSubmit } = useForm();

  const [state, setState] = useState({
    supported: false,
    error: null,
  });

  useEffect(() => {
    setState({
      ...state,
      supported: supported(),
    });
  }, []);

  const onSubmit = async (data: any) => {
    try {
      const result = await registration(data.email, data.displayName);
      console.log(result);
    } catch (e: any) {
      setState({ ...state, error: e.statusText });
    }
  };

  if (!state.supported) {
    return <div>Not Supported</div>;
  }

  return <>
    {state.error && <Alert icon={<GppBadIcon fontSize="inherit" />} severity="error">
      {state.error}
    </Alert>}

    <Stack
      spacing={1}
      component="form"
      onSubmit={handleSubmit(onSubmit)}
      noValidate
      autoComplete="off"
    >
      <Typography textAlign={'center'} variant={'h5'}>Registration</Typography>
      <TextField {...register('email')}
                 id={'email'}
                 key={'email'}
                 name={'email'}
                 placeholder={'Email'}
                 label={'Email'}
                 size="small"
      />
      <TextField {...register('displayName')}
                 id={'displayName'}
                 key={'displayName'}
                 name={'displayName'}
                 placeholder={'Display Name'}
                 label={'Display Name'}
                 size="small"
      />
      <Button size="small" variant="contained" type="submit">Login</Button>
    </Stack>
  </>;
}

export default RegistrationForm;
